<template>
	<div>
		<h1 class="text-center">Registrar nuevo libro</h1>

		<v-form v-model="valid" ref="form">
			<v-container>
				<v-row>
					<v-col cols="12">
						<v-text-field
							v-model="form.title"
							:rules="requiredRules"
							:counter="35"
							label="Nombre Libro"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12">
						<v-text-field
							v-model="form.author"
							:rules="requiredRules"
							:counter="20"
							label="Autor"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12">
						<v-text-field
							v-model="form.image"
							:rules="requiredRules"
							label="Src"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12">
						<v-select 
            :items="types" 
            label="Tipo" 
            v-model="form.type" 
            :rules="requiredRules"></v-select>
					</v-col>
                    <v-col class="text-center">
                        <v-btn class="btn"
                        color="deep-purple lighten-2" dark 
                        @click="createBook"
                        >Registrar</v-btn>
                        <v-btn class="btn"
                        color="indigo lighten-1" dark 
                        @click="resetForm"
                        >Limpiar formulario</v-btn>
                    </v-col>
				</v-row>
			</v-container>
		</v-form>
	</div>
</template>

<script>
import {mapState} from 'vuex'
import {db} from '@/firebase/firebase.js'
import { collection, addDoc } from 'firebase/firestore'
export default {
	name: 'register-book',
	// props: {},
	data: function () {
		return {
			valid: true,
			form: {
				title: '',
				author: '',
				type: '',
				image: '',
			},
			requiredRules: [(v) => !!v || 'Campo es requerido'],
		}
	},
	computed: {
        ...mapState(['types'])
    },
	methods: {
        async createBook(){
            this.$refs.form.validate()//validar campos
            if(!this.valid) return //no es valido y funcion se cierra
            try{
                let response =  await addDoc(collection(db,'books'), this.form)
                console.log(response)
                this.$router.push('/')
            }
            catch(error){
                console.log(error)
            }
        },
        resetForm(){
            this.$refs.form.reset()
        }
    }
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	// -- End Lifecycle Methods
}
</script>

<style scoped>
form {
	display: grid;
	width: 600px;
	margin: 0 auto;
}
.btn{
  margin: 10px;
}
</style>
