<template>
    <v-container>
        <v-row justify="center">
            <v-col md="6">
                <h2>Ingreso Usuario Registrado</h2>
                <v-form>
                    <v-text-field 
                    v-model="user" 
                    type="email" 
                    label="Correo">
                    </v-text-field>

                    <v-text-field 
                    v-model="pass" 
                    type="password" 
                    label="Contraseña">
                    </v-text-field>
                    <v-btn @click="login"
                    color="indigo lighten-1" dark block
                    >Ingresar
                    </v-btn>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth'
export default {
    name: 'login-component',
    // props: {},
    data: function(){
        return {
            user:'',
            pass:''
        }
    },
    // computed: {},
    methods: {
        login(){
            const auth = getAuth()
            signInWithEmailAndPassword(auth, this.user, this.pass)
                .then((userCredential) => {
                    // Signed in
                    const user = userCredential.user;
                    console.log(user)
                    this.$router.push('/')
                    // ...
                })
                .catch((error) => {
                    // const errorCode = error.code;
                    const errorMessage = error.message;
                    alert(errorMessage)
                });

        }
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

<style scoped>
    h2{
    margin: 40px;
}
</style>